/*Handling background particles*/
.particles{
    position: relative;
    height: 95vh;
    width: 100%;
    background-color: #0c0c0c;
    background-position: top;
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
            clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    /* clip-path: polygon(59% 0, 100% 0, 100% 35%, 100% 70%, 100% 100%, 84% 76%, 0 100%, 0% 70%, 0% 35%, 0 0);} */
}
.background{
    height: 95vh;
}

.background-title{
    position: absolute;
    z-index: 1;
    font-family: 'Bitter', serif;
    top: 35%;
    left: 10%;
    -webkit-animation: moveInUp 2s;
            animation: moveInUp 2s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.background-title > h1{
    text-align: left;
    display: block;
    color: white;
    font-size: 6vw;
    letter-spacing: 2px;
}
.background-title > h3{
    /* color: deeppink; */
    color: cyan;
    margin-left: 10px;
    text-align: left;
    font-size: 2vw;
}
