.navbar{
    position: fixed;
    -webkit-backdrop-filter: saturate(180%) blur(5px);
            backdrop-filter: saturate(180%) blur(5px);
    background-color: hsl(0, 0%, 10%);
    /* display: flex;
    flex-direction: row; */
    z-index: 1000;
    height: 3.5em;
    width: 100%;
    color:rgb(187, 187, 187);
    font-size: 16px;
    font-family: 'Bitter', serif;
    letter-spacing: 2px;
    line-height: 3.5em;
}

.resume-menu{
    position: absolute;
    margin-left: 1em;
}

.burger-menu{
    display: none;
}

.resume-menu a{
    /* font-size: 1.5vw; */
    white-space: nowrap;
    text-decoration: none;
    color: #0c0c0c;
    background-color: rgb(0, 255, 255);
    padding: 0.8em;
}

.menu{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.main-menu{
    /* width: 100%; */
    flex: 9 1;
    margin-left: 9vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.main-menu a{
    cursor: pointer;
    margin: 0 2vw 0 2vw;
    display: inline-block;
    transition: 0.5s;
}

.main-menu a:hover{
    color: white;
}

.icons-menu{
    flex: 1 1;
