    background: url(/Portfolio/static/media/linkedin.beb00edb.svg) no-repeat;
}

/*Media Queries*/
@media screen and (max-width: 850px) {
    .navbar{
        line-height: 2.2em;
    }
    .resume-menu{
        margin-top: 0.6em;
    }
    .line{
        width: 3.5vw;
        height: 3px;
        background: lightgray;
        margin: 5px;
    }
    .burger-menu{
        display: block;
        top: 0;
        right: 0;
        position: fixed;
        cursor: pointer;
        padding: 1.3vh;
        z-index: 100;
        border-radius: 8px;
    }
    .burger-menu div{
        transition: all 0.5s ease;
    }
    .menu{
        -webkit-backdrop-filter: saturate(180%) blur(5px);
                backdrop-filter: saturate(180%) blur(5px);
        background-color: hsla(0, 0%, 10%, 0.609);
        flex-direction: column;
        width: 40%;
        height: 100vh;
        float: right;
        transform: translateX(100%);
        transition: transform 0.5s ease-in;
    }
    .main-menu{
        flex-direction: column;
        margin: 0;
        flex: 2 1;
        line-height: 14vw;
    }
    
    .icons-menu{
        margin-top: 0;
    } 
    .github{
        margin-left: 10vw;
    }
}

