  }
  
  #about p {
    padding-bottom: 1.5rem;
    color: #0c0c0c;
    line-height: 1.9rem;
    
  }
  #me-img{
    width: 400px;
    height: 400px;
    border-radius: 50%;
    box-shadow: 0 20px 10px -10px #25293450;
}

  
  #about .desc.full {
    grid-column-end: span 4;
    margin-bottom: 2rem;
  }
  
  #about .desc {
    grid-column-end: span 2;
    background: #1d151517;
   
    padding: 2rem;
    text-align: center;
  }
  #about .desc > p {
    text-align: center;
    margin-top: 6vh;
  }
  
  @media only screen and (max-width: 1149px) {
    #about{
      background-size: 100vh; 
    }
    #about article {
      grid-template-columns: 1fr;
      grid-gap: 0;
    }
    #about .desc.full {
      grid-column-end: -1;
    }
  
    #about .desc {
      grid-column-end: -1;
    }
  }
  @media only screen and (max-width: 949px) {
    #about {
      background-position: top left;
      background-size: cover;
    }
  }
  @media only screen and (max-width: 420px) {
    #about{
      font-size: 1.2rem;
