/* Single Project */
.project {
    margin: 1rem auto;
    width: 80%;
    padding: 3rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 5rem;
    color: rgb(216, 216, 216);
  }
  
  /* Project Image */

.project-link {
    display: block;
    margin: auto 0;
    color: #252934;
    overflow: hidden;
    text-align: center;
    border-radius: 50%;
    border: 1px solid black;
    box-shadow: 0 20px 10px -10px #25293450;
    transition: 300ms;
  }
.project-title{
  color: aqua;
  font-family: 'Libre Baskerville, serif';
  font-size: 2em;
}
.project-link:hover {
    box-shadow: 0 50px 15px -30px #25293450;
